
































import { Component, Vue, Watch } from 'vue-property-decorator';
import {Radio, RadioGroup, Button} from 'vant';
import ScanSign from '@/componets/scan_sign.vue';
import {notes, NoteType} from '@/note';
import {Business} from '@/entry/diamond';

import 'vant/lib/radio-group/style';
import 'vant/lib/radio/style';
import 'vant/lib/button/style';

Vue.use(RadioGroup).use(Radio).use(Button);
@Component({
    components: {ScanSign},
})
export default class Input extends Vue {


    public choiced = '授权业务';
    public inputName = '';
    public inputAddr = '';
    public signText = '';
    public showQr = false;
    public frequency = 1000;
    public bgColor = '#DFF4EF';

    @Watch('choiced', {immediate: true, deep: true})
    public onChoicedChange(val: string):void {
        if (val == '授权业务') {
            this.bgColor = '#DFF4EF';
        } else {
            this.bgColor = '#EED1D1';
        }
    }


  public created(): void {
    this.$store.state.title = this.$t('business.input');
  }
  public qrCode():void{
        // todo 增加地址规则

      if (this.inputName && this.inputAddr) {
          this.signText = this.inputName + ',' + this.inputAddr + ',' + this.choiced;
          if (this.showQr == false) {
              this.showQr = true;
          } else {
              this.showQr = false;
          }
      } else {
          notes.add({type: NoteType.Error, message: this.$t('notifyMsg.inputNotNull').toString()});
      }
  }
  public scanSigned():void{
        // todo 获取签名后二维码内容
      const business = new Business();
      // todo 构造business
      business.auth = true;
      business.authAddress = this.inputAddr;
      business.name = this.inputName;
      business.count = this.frequency;
      this.$store.state.data.diamond.chainBusiness(business).then((status: string) => {
          if (status == 'Success') {

              this.$store.state.data.businessList.push(business);
              this.$router.push({name: 'business_process', params: {name: this.inputName, addr: this.inputAddr, choice: this.choiced}});

          }
      });

  }

}
